.App {
  margin-top: 15px;
}

.App .navbar-brand {
  font-weight: bold;
}

.chatbot {
  width: 100%;
  overflow-y: scroll;
  float: right;
}

/*.w-text {
  align-self: baseline;
}*/

.App.chatbot {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.lex {
  overflow-y: scroll;
  margin-bottom: 20px;
  max-height: 350px;
  min-height: 350px;
  display: flex;
  flex-direction: column;
  padding: 10px;
  align-items: center;
  width: 500px;
  justify-content: center;
  align-self: center;
  position: relative;
}

#record {
  margin-top: 50px;
  display: flex;
  justify-content: center;
}

.fb-images {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.timeline {
  padding: 10px;
  width: 100px;
  height: 100px;
}

.record-it {
    align-self: flex-end;
    position: relative;
    top: -32px;
    font-size: 19px;
    right: 61px;
    padding: 5px;
    border: 0px;
}

.cont {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.logout-btn {
    display: inline-block;
    padding: 2px 2px;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.42857143;
    /* text-align: center; */
    white-space: nowrap;
    vertical-align: middle;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-image: none;
    border: 1px solid transparent;
    background-color: #f8f8f8;
    border-radius: 11px;
}

.Home .lander {
  padding: 80px 0;
  text-align: center;
}

.Home .lander h1 {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
}

.Home .lander p {
  color: #999;
}
.LoaderButton .spinning.glyphicon {
  margin-right: 7px;
  top: 2px;
  -webkit-animation: spin 1s infinite linear;
          animation: spin 1s infinite linear;
}
@-webkit-keyframes spin {
  from { -webkit-transform: scale(1) rotate(0deg); transform: scale(1) rotate(0deg); }
  to { -webkit-transform: scale(1) rotate(360deg); transform: scale(1) rotate(360deg); }
}
@keyframes spin {
  from { -webkit-transform: scale(1) rotate(0deg); transform: scale(1) rotate(0deg); }
  to { -webkit-transform: scale(1) rotate(360deg); transform: scale(1) rotate(360deg); }
}
@media all and (min-width: 480px) {
  .Login {
    padding: 60px 0;
  }

  .Login form {
    margin: 0 auto;
    max-width: 320px;
  }
}
.NotFound {
  padding-top: 100px;
  text-align: center;
}
@media all and (min-width: 480px) {
  .Signup {
    padding: 60px 0;
  }

  .Signup form {
    margin: 0 auto;
    max-width: 320px;
  }
}

.Signup form span.help-block {
  font-size: 14px;
  padding-bottom: 10px;
  color: #999;
}
